import { KeyboardControls } from "./KeyboardControls";
import { IInputControls } from "./IInputControls";

const { ccclass, property } = cc._decorator;

@ccclass
export default class PlayerController extends cc.Component {
    @property(cc.Animation)
    anim: cc.Animation = null;

    @property
    speed: number = 200;
    
    @property(cc.Prefab)
    doughPrefab: cc.Prefab = null;


    private input: IInputControls = null;
    private currentAnim: string = "";
    private lastDir: cc.Vec2 = cc.v2(0, -1); // é è¨­å‘ä¸‹ï¼ˆå‰ï¼‰
    private rb: cc.RigidBody = null;

    private canPickDough: boolean = false;
    private canDropDough: boolean = false;
    private carriedDough: cc.Node = null;
    private currentDropTarget: cc.Node = null;



    start() {
        if (!this.anim) {
            this.anim = this.getComponent(cc.Animation);
        }

        this.rb = this.getComponent(cc.RigidBody);
        if (!this.rb) {
            cc.error("âŒ æ‰¾ä¸åˆ° RigidBody2D çµ„ä»¶ï¼");
            return;
        }

        this.playAnim("girl_idle_back");
        this.input = new KeyboardControls();
    }

    update(dt: number) {
        const dir = this.input.getMoveDirection();

        if (!dir.equals(cc.Vec2.ZERO)) {
            // ä½¿ç”¨ç‰©ç†å¼•æ“çš„ linearVelocity æ§åˆ¶ç§»å‹•
            this.rb.linearVelocity = dir.clone().normalize().mul(this.speed);
            this.lastDir = dir.clone();

            // å‹•ç•«åˆ‡æ›ï¼ˆèˆ‡æ–¹å‘åˆ¤æ–·ç„¡è®Šï¼‰
            if (Math.abs(dir.y) > Math.abs(dir.x)) {
                if (dir.y > 0) {
                    this.playAnim("girl_walk_back");
                } else {
                    this.playAnim("girl_idle_walk");
                }
                this.node.scaleX = 1;
            } else {
                if (dir.x > 0) {
                    this.playAnim("girl_walk_right");
                    this.node.scaleX = 1;
                } else {
                    this.playAnim("girl_walk_left");
                    this.node.scaleX = 1;
                }
            }
        } else {
            // åœæ­¢ç§»å‹•ï¼ˆé€Ÿåº¦æ­¸é›¶ï¼‰
            this.rb.linearVelocity = cc.v2(0, 0);

            // æ’­æ”¾éœæ­¢å‹•ç•«
            if (Math.abs(this.lastDir.y) > Math.abs(this.lastDir.x)) {
                this.playAnim(this.lastDir.y > 0 ? "girl_idle_back" : "girl_idle");
            } else {
                this.playAnim(this.lastDir.x > 0 ? "girl_idle_right" : "girl_idle_left");
            }
            this.node.scaleX = 1;
        }

        if (this.input.getInteractPressed()) {
            this.tryInteract();
        }

    }

    tryInteract() {
        if (this.canPickDough && !this.carriedDough) {
            // ç”Ÿæˆéºµåœ˜
            const dough = cc.instantiate(this.doughPrefab);
            this.node.addChild(dough);  // æˆ–ç”¨å…¶ä»– attach æ–¹å¼
            dough.setPosition(cc.v2(0, 50)); // æ”¾åœ¨è§’è‰²é ­ä¸Šæ–¹ï¼ˆå¾®èª¿ï¼‰
            this.carriedDough = dough;
            console.log("æ‹¿èµ·éºµåœ˜");
        } else if (this.canDropDough && this.carriedDough && this.currentDropTarget) {
            this.carriedDough.parent = this.node.parent;

            const worldPos = this.currentDropTarget.convertToWorldSpaceAR(cc.v2(0, 0));
            const localPos = this.node.parent.convertToNodeSpaceAR(worldPos);

            // å¾®èª¿é«˜åº¦èˆ‡ Z
            this.carriedDough.setPosition(cc.v3(localPos.x - 505, localPos.y -300, 100));

            this.carriedDough = null;
            console.log("ğŸ¥¯ æ”¾ä¸‹éºµåœ˜æˆåŠŸï¼");
        }



    }


    playAnim(name: string) {
        if (this.currentAnim === name) return;
        this.currentAnim = name;
        this.anim.play(name);
    }

    onBeginContact(contact, selfCollider, otherCollider) {
        if (otherCollider.tag === 3) {
            this.canPickDough = true;
            console.log("å¯ä»¥æ’¿èµ·éºµåœ˜");
        } else if (otherCollider.tag === 8) {
            this.canDropDough = true;
            this.currentDropTarget = otherCollider.node; // â¬…ï¸ æŠŠç §æ¿è¨˜ä¸‹ä¾†
            console.log("å¯ä»¥æ”¾ä¸‹éºµåœ˜åœ¨", this.currentDropTarget.name);
        }
    }


    onEndContact(contact, selfCollider, otherCollider) {
        if (otherCollider.tag === 3) {
            this.canPickDough = false;
        } else if (otherCollider.tag === 8) {
            this.canDropDough = false;
            this.currentDropTarget = null; // â¬…ï¸ é›¢é–‹ç §æ¿æ™‚æ¸…é™¤
        }
    }



}
